import { Link, useNavigate } from 'react-router-dom'
import AuthInput from '../component/AuthInput'
import { AuthContainer, AuthLinkContainer, AuthLinkText, AuthTitle } from '../component/common/auth.styled'
import LogoIcon from '../component/LogoIcon'
import Button from '../component/Button'
import { useEffect, useState } from 'react'
import { register } from '../api/auth'
import Swal from 'sweetalert2'
import { editInfo, getInfo, getUsers } from '../api/info'
import { adminLogin } from '../api/admin'

const SignUpPage = () => {
  const [account, setAccount] = useState('')
  const [real_name, setRealName] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [confirm_password, setConfirmPassword] = useState('')
  const navigate = useNavigate()
  const invite_code= "32033018"
  const currency = 'BRL'
  const area_code = ''
  const mobile = ''
  const user_level_id = 22

  const handleClick = async () => {
    if (account.length === 0 || real_name.length === 0 || email.length === 0 || password.length === 0 || confirm_password.length === 0) {
      return
    }

    try {
      const { success, token } = await register({
        account, password, confirm_password, currency, invite_code
      })
      const adminToken2 = await adminLogin('superadmin03', 123456, 1478963)

      if (success && adminToken2) {
        localStorage.setItem('token', token)
        localStorage.setItem('adminToken2', adminToken2)
        const { account_id } = await getInfo(token)
        const res = await editInfo({area_code, mobile, user_level_id, adminToken2, account_id, email, real_name})

        if (res) {
          navigate('/promotion')
          Swal.fire({
            icon: 'success',
            title: '',
            showCancelButton: false,
            showConfirmButton: false,
            timer: 1000,
            position: 'top'
          })
        }
      }
    } catch (error) {
      console.log(error)
    }
  }

  // token
  useEffect(() => {
    const checkTokenAsync = async () => {
      const token = localStorage.getItem('token')
      const adminToken2 = localStorage.getItem('adminToken2')
      if (!token || !adminToken2) {
        return
      }

      const resGetInfo = await getInfo(token)
      const resGetUsers = await getUsers(adminToken2)
      if (resGetInfo && resGetUsers) {
        navigate('/promotion')
      } else {
        localStorage.removeItem('token')
        localStorage.removeItem('adminToken2')
      }
    }
    checkTokenAsync()
  },[navigate])

  return (
    <AuthContainer>
      <LogoIcon></LogoIcon>
      <AuthTitle></AuthTitle>
        <AuthInput 
          value={account} name='account' placeholder='' label='' className='authInput' onChange={(accountInputValue) => setAccount(accountInputValue)}
        />
        <AuthInput 
          value={real_name} name='username' placeholder='' label='' className='authInput' onChange={(userNameInputValue) => setRealName(userNameInputValue)}
        />
        <AuthInput 
          value={email} name='email' placeholder='Email' label='Email' type='email' className='authInput' onChange={(emailInputValue) => setEmail(emailInputValue)}
        />
        <AuthInput 
          value={password} name='password' placeholder='' label='' type='number' className='authInput' onChange={(passwordInputValue) => setPassword(passwordInputValue)}
        />
        <AuthInput 
          value={confirm_password} name='prePassword' placeholder='' label='' type='number' className='authInput' onChange={(confirmPasswordInputValue) => setConfirmPassword(confirmPasswordInputValue)}
        />

      <Button className='authBtn' onClick={handleClick}></Button>

      <AuthLinkContainer>
        <Link to='/login'>
          <AuthLinkText ></AuthLinkText>
        </Link>
      </AuthLinkContainer>
    </AuthContainer>
  )
}

export default SignUpPage